import {Component} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Product} from "./Model/Product";
import {computeStyle} from "@angular/animations/browser/src/util";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  public product: Product = new Product();
  public products;
  public isList = true;
  public isAdd = true;

  constructor(private http: HttpClient) {
    http.get("http://localhost:8080/products").subscribe(data => {
      this.products = data;
    });
  }

  public deleteContact(id: any) {
    this.http.delete("http://localhost:8080/products/" + id).subscribe(() =>
      this.http.get("http://localhost:8080/products").subscribe(data => {
        this.products = data;
      })
    );
  }

  public modify(id: number) {
    this.products.forEach((c: Product) => {
      if (c.id == id) {
        this.product = c;
      }
    });
    this.isAdd = false;
    this.flip();
  }

  public addModifyContact() {
    this.product.label = (<HTMLInputElement>document.getElementById("i1")).value;
    this.product.quantity = +(<HTMLInputElement>document.getElementById("i2")).value;
    this.product.unitPrice = +(<HTMLInputElement>document.getElementById("i3")).value;
    this.product.lastUpdate = (<HTMLInputElement>document.getElementById("i4")).value;
    this.http.post("http://localhost:8080/products", this.product).subscribe(() =>
      this.http.get("http://localhost:8080/products").subscribe(data => {
        this.products = data;
      })
    );
    this.flip();

  }

  public flip() {
    this.isList = !this.isList;
  }

  public addContact() {
    this.product = new Product();
    this.flip();
    this.isAdd = true;
  }

  public search() {
    let mc = (<HTMLInputElement>document.getElementById("searchId")).value;
    this.http.get("http://localhost:8080/products/search?msg=" + mc + "&size=500").subscribe(data => this.products = data['content']);
  }
}
